@import url(https://fonts.googleapis.com/css2?family=Work+Sans&display=swap);
.sidenav {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.profile-image {
  width: 50px;
  height: 50px;
  border-radius: 50%;
  background-color: rgb(255, 255, 255);
  margin-top: 10vh;
}

.sidenavHeading {
  margin: 1rem;
}

/* .search {
  display: block;
} */

.search-input {
  height: 4vh;
  width: 13vw;
  border-radius: 1rem;
  margin: 1rem;
  display: flex;
  background-color: rgb(65, 65, 65);
  outline: none;
  border: none;
  display: flex;
  justify-content: center;
  padding-left: 1rem;
}

.create-note {
  margin: 1rem;
}

.logout-btn {
  height: 4vh;
  width: 13vw;
  border-radius: 1rem;
  margin: 1rem;
  display: flex;
  background-color: rgb(26, 170, 26);
  outline: none;
  border: none;
  display: flex;
  justify-content: center;
  padding-left: 1rem;
  color: rgb(255, 255, 255);
  text-align: center;
  align-items: center;
}

.logout-btn:hover {
  background-color: rgb(103, 206, 0);
}

@media only screen and (max-width: 800px) {
  .create-note button {
    width: 60vw;
    max-width: 200px;
    display: block;
  }
  .search-input {
    width: 50vw;
  }
  .logout-btn {
    width: 50vw;
  }
}

.quillm {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 5vh;
}

.create-note-btn {
  margin-top: 5vh;
  background-color: rgb(26, 170, 26);
  height: 5vh;
  width: 13vw;
  border-radius: 1rem;
  /* margin: 1rem; */
  color: rgb(255, 255, 255);
  outline: none;
  border: none;
}

.create-note-btn:hover {
  background-color: rgb(103, 206, 0);
}

.react-quill {
  width: 59vw;
}

.quill {
  width: 59vw;
  height: 70vh;
  margin-top: 5vh;
}
.title input {
  border: none;
  border-bottom: 2px solid rgb(103, 206, 0);
}

.input-title {
  font-size: 20px;
}

.title input:focus {
  outline: none;
}

.notes {
  max-height: 100vh;
  display: flex;
  flex-direction: column;
}
.notelist {
  display: flex;
  overflow-y: scroll;
  flex-direction: column;
}

.note-heading {
  margin-left: 1rem;
}
@media only screen and (max-width: 800px) {
  .notelist {
    width: 100%;
  }
}

.d-icon:hover {
  color: rgb(197, 8, 8);
}

.app {
  display: flex;
  flex-wrap: wrap;
}

.sidenav-app {
  width: 20%;
  background-color: rgb(11, 12, 12);
  height: 100vh;
  color: rgb(82, 82, 82);
}

.quill-app {
  height: 100vh;
  width: 59vw;
  display: flex;
  margin: 0 auto;
}

.title-input {
  border: none;
}

.all-notes {
  width: 20%;
  background-color: rgb(223, 223, 223);
}
@media only screen and (max-width: 800px) {
  /* .app {
    display: flex;
    flex-direction: column;
  } */
  Body {
    margin: 0;
    padding: 0;
  }
  .sidenav-app {
    width: 100vw;
    display: block;
  }
  .quill-app {
    display: block;
    max-width: 100vw;
    margin: 0;
    padding: 0;
  }

  .input-title {
    margin-left: 20vw;
    display: flex;
  }

  .create-note-btn {
    margin-left: 20vw;
  }

  .quill {
    display: block;
    width: 97vw;
    max-width: 100vw;
    /* margin: 0 auto; */
  }

  .all-notes {
    width: 100%;
  }
}

body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen,
    Ubuntu, Cantarell, Fira Sans, Droid Sans, Helvetica Neue, sans-serif;
  text-rendering: optimizeLegibility;
}

